'use client'

/**
 * 词库系统测试页面
 * 用于测试和演示词库功能
 */

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Volume2, Search, Shuffle, Check, X } from 'lucide-react'
import {
    ALL_WORDBOOKS,
    getWordbookById,
    getSectionWords,
    searchWords,
    getRandomWords,
    validateSpelling
} from '@/lib/utils/wordbook-manager'
import { speakWord, speakSentence, stopSpeaking } from '@/lib/utils/text-to-speech'
import type { Word } from '@/lib/types/word'

export default function WordbookTestPage() {
    const [selectedWordbook, setSelectedWordbook] = useState('cet4_wordbook')
    const [currentWord, setCurrentWord] = useState<Word | null>(null)
    const [searchQuery, setSearchQuery] = useState('')
    const [searchResults, setSearchResults] = useState<Word[]>([])
    const [spellingInput, setSpellingInput] = useState('')
    const [spellingResult, setSpellingResult] = useState<any>(null)

    // 加载第一个单词
    useEffect(() => {
        loadRandomWord()
    }, [selectedWordbook])

    const loadRandomWord = () => {
        const wordbook = getWordbookById(selectedWordbook)
        if (!wordbook || !wordbook.sections[0]) return

        const words = getSectionWords(selectedWordbook, wordbook.sections[0].id)
        if (words.length > 0) {
            const randomWord = words[Math.floor(Math.random() * words.length)]
            setCurrentWord(randomWord)
            setSpellingInput('')
            setSpellingResult(null)
        }
    }

    const handleSearch = () => {
        if (!searchQuery.trim()) {
            setSearchResults([])
            return
        }

        const results = searchWords(searchQuery, {
            difficulty: selectedWordbook === 'cet4_wordbook' ? 'CET4' : 'CET6',
            limit: 10
        })
        setSearchResults(results)
    }

    const handleSpellingCheck = () => {
        if (!currentWord) return

        const result = validateSpelling(spellingInput, currentWord.word)
        setSpellingResult(result)
    }

    const handlePlayWord = () => {
        if (currentWord) {
            speakWord(currentWord.word)
        }
    }

    const handlePlayExample = (example: string) => {
        speakSentence(example)
    }

    return (
        <div className="min-h-screen bg-[#FAFAFA] p-8">
            <div className="max-w-6xl mx-auto space-y-8">
                {/* Header */}
                <div className="text-center">
                    <h1 className="text-4xl font-bold text-gray-900 mb-2">
                        📚 词库系统测试页面
                    </h1>
                    <p className="text-gray-600">
                        测试 CET-4/6 词库功能和 Web Speech API
                    </p>
                </div>

                {/* Wordbook Selector */}
                <Card className="p-6">
                    <h2 className="text-xl font-semibold mb-4">选择词书</h2>
                    <div className="flex gap-4">
                        {ALL_WORDBOOKS.map((wb) => (
                            <Button
                                key={wb.id}
                                onClick={() => setSelectedWordbook(wb.id)}
                                variant={selectedWordbook === wb.id ? 'default' : 'outline'}
                                className={selectedWordbook === wb.id ? 'bg-[#E85D75] hover:bg-[#D54D65]' : ''}
                            >
                                {wb.name} ({wb.totalWords}词)
                            </Button>
                        ))}
                    </div>
                </Card>

                {/* Current Word Display */}
                {currentWord && (
                    <Card className="p-8">
                        <div className="flex items-start justify-between mb-6">
                            <div>
                                <div className="flex items-center gap-4 mb-2">
                                    <h2 className="text-5xl font-bold text-gray-900">
                                        {currentWord.word}
                                    </h2>
                                    <Button
                                        onClick={handlePlayWord}
                                        variant="outline"
                                        size="icon"
                                        className="rounded-full"
                                    >
                                        <Volume2 className="w-5 h-5" />
                                    </Button>
                                </div>
                                <p className="text-gray-500 font-mono text-lg">
                                    {currentWord.phonetic}
                                    {currentWord.phoneticUK && ` / ${currentWord.phoneticUK}`}
                                </p>
                            </div>
                            <Button
                                onClick={loadRandomWord}
                                variant="outline"
                                className="gap-2"
                            >
                                <Shuffle className="w-4 h-4" />
                                随机单词
                            </Button>
                        </div>

                        <div className="space-y-4">
                            <div>
                                <h3 className="text-sm font-semibold text-gray-500 mb-1">释义</h3>
                                <p className="text-xl text-gray-900">{currentWord.translation}</p>
                            </div>

                            {currentWord.examples.length > 0 && (
                                <div>
                                    <h3 className="text-sm font-semibold text-gray-500 mb-2">例句</h3>
                                    {currentWord.examples.map((example, idx) => (
                                        <div key={idx} className="bg-[#FFF5F8] rounded-lg p-4 mb-2">
                                            <div className="flex items-start gap-2 mb-2">
                                                <span className="text-[#E85D75] font-bold text-xs mt-1">EN</span>
                                                <p className="text-gray-700 flex-1">{example.english}</p>
                                                <Button
                                                    onClick={() => handlePlayExample(example.english)}
                                                    variant="ghost"
                                                    size="icon"
                                                    className="h-6 w-6"
                                                >
                                                    <Volume2 className="w-4 h-4" />
                                                </Button>
                                            </div>
                                            <div className="flex items-start gap-2 border-t border-[#FFD6E0] pt-2">
                                                <span className="text-[#E85D75] font-bold text-xs mt-1">CN</span>
                                                <p className="text-gray-600">{example.chinese}</p>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                            )}

                            {currentWord.tags && currentWord.tags.length > 0 && (
                                <div>
                                    <h3 className="text-sm font-semibold text-gray-500 mb-2">标签</h3>
                                    <div className="flex gap-2">
                                        {currentWord.tags.map((tag, idx) => (
                                            <span
                                                key={idx}
                                                className="px-3 py-1 bg-gray-100 text-gray-700 rounded-full text-sm"
                                            >
                                                {tag}
                                            </span>
                                        ))}
                                    </div>
                                </div>
                            )}

                            {(currentWord.synonyms || currentWord.antonyms) && (
                                <div className="grid grid-cols-2 gap-4">
                                    {currentWord.synonyms && currentWord.synonyms.length > 0 && (
                                        <div>
                                            <h3 className="text-sm font-semibold text-gray-500 mb-1">同义词</h3>
                                            <p className="text-gray-700">{currentWord.synonyms.join(', ')}</p>
                                        </div>
                                    )}
                                    {currentWord.antonyms && currentWord.antonyms.length > 0 && (
                                        <div>
                                            <h3 className="text-sm font-semibold text-gray-500 mb-1">反义词</h3>
                                            <p className="text-gray-700">{currentWord.antonyms.join(', ')}</p>
                                        </div>
                                    )}
                                </div>
                            )}
                        </div>
                    </Card>
                )}

                {/* Spelling Test */}
                {currentWord && (
                    <Card className="p-6">
                        <h2 className="text-xl font-semibold mb-4">拼写测试</h2>
                        <div className="flex gap-4">
                            <Input
                                value={spellingInput}
                                onChange={(e) => setSpellingInput(e.target.value)}
                                onKeyDown={(e) => e.key === 'Enter' && handleSpellingCheck()}
                                placeholder="输入单词拼写..."
                                className="flex-1"
                            />
                            <Button onClick={handleSpellingCheck} className="bg-[#E85D75] hover:bg-[#D54D65]">
                                检查
                            </Button>
                        </div>
                        {spellingResult && (
                            <div className="mt-4 p-4 rounded-lg bg-gray-50">
                                <div className="flex items-center gap-2 mb-2">
                                    {spellingResult.isCorrect ? (
                                        <>
                                            <Check className="w-5 h-5 text-green-600" />
                                            <span className="text-green-600 font-semibold">正确！</span>
                                        </>
                                    ) : (
                                        <>
                                            <X className="w-5 h-5 text-red-600" />
                                            <span className="text-red-600 font-semibold">错误</span>
                                        </>
                                    )}
                                </div>
                                <p className="text-gray-700">
                                    相似度: {(spellingResult.similarity * 100).toFixed(1)}%
                                </p>
                                {!spellingResult.isCorrect && (
                                    <p className="text-gray-600 mt-2">
                                        正确拼写: <strong>{currentWord.word}</strong>
                                    </p>
                                )}
                            </div>
                        )}
                    </Card>
                )}

                {/* Search */}
                <Card className="p-6">
                    <h2 className="text-xl font-semibold mb-4">搜索单词</h2>
                    <div className="flex gap-4 mb-4">
                        <Input
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            onKeyDown={(e) => e.key === 'Enter' && handleSearch()}
                            placeholder="输入单词或释义..."
                            className="flex-1"
                        />
                        <Button onClick={handleSearch} className="gap-2">
                            <Search className="w-4 h-4" />
                            搜索
                        </Button>
                    </div>

                    {searchResults.length > 0 && (
                        <div className="space-y-2">
                            <p className="text-sm text-gray-500">找到 {searchResults.length} 个结果</p>
                            {searchResults.map((word) => (
                                <div
                                    key={word.id}
                                    className="p-4 border border-gray-200 rounded-lg hover:bg-gray-50 cursor-pointer"
                                    onClick={() => setCurrentWord(word)}
                                >
                                    <div className="flex items-center justify-between">
                                        <div>
                                            <span className="font-semibold text-lg">{word.word}</span>
                                            <span className="text-gray-500 ml-3">{word.phonetic}</span>
                                        </div>
                                        <Button
                                            onClick={(e) => {
                                                e.stopPropagation()
                                                speakWord(word.word)
                                            }}
                                            variant="ghost"
                                            size="icon"
                                        >
                                            <Volume2 className="w-4 h-4" />
                                        </Button>
                                    </div>
                                    <p className="text-gray-700 mt-1">{word.translation}</p>
                                </div>
                            ))}
                        </div>
                    )}
                </Card>

                {/* System Info */}
                <Card className="p-6 bg-blue-50 border-blue-200">
                    <h2 className="text-xl font-semibold mb-4 text-blue-900">系统信息</h2>
                    <div className="space-y-2 text-sm text-blue-800">
                        <p>✅ 词库系统已加载</p>
                        <p>✅ Web Speech API 可用</p>
                        <p>📚 当前词书: {ALL_WORDBOOKS.find(wb => wb.id === selectedWordbook)?.name}</p>
                        <p>📊 总单词数: {ALL_WORDBOOKS.reduce((sum, wb) => sum + wb.totalWords, 0)}</p>
                    </div>
                </Card>
            </div>
        </div>
    )
}
